.inputtdrag::after {
    position: absolute;
    background-image: "url('../public/img/camera.png')";
    width: 100%;
    content: 'Upload NFT';
    top: 0px;
    color: white;
    text-align: center;
    margin: auto;
    display: flex;
    align-content: center;
    justify-content: center;
    padding: 17px 0px;
    border-radius: 7px;
    border: 2px solid #c1c1c1;
    border-style: dashed;
    z-index: 1;
    padding: 46% 0px;
}
.inputdragVedio::after {
    position: absolute;
    background-color: transparent;
    width: 100%;
    content: 'Upload NFT';
    top: 0px;
    color: white;
    text-align: center;
    margin: auto;
    display: flex;
    align-content: center;
    justify-content: center;
    padding: 17px 0px;
    border-radius: 7px;
    border: 2px solid #c1c1c1;
    border-style: dashed;
    z-index: 1; 
    background-color: #16151a;
}
.inputdragVedio .inputtdrag::after {
    content: '' !important;
}

.inputtdrag {
    height: auto;
    position: relative;
    height: 300px;
}
.inputtdrag input{
    z-index: 999;
    position: absolute;
    opacity: 0;
    max-width: 300px;
    width: 100%;
    height: 300px;
    cursor: pointer;
}
.nft-image {
    z-index: 2;
    position: absolute;
    width: 96%;
    height: 97%;
    object-fit: contain;
    background-color: black;
    margin: 6px;
}
.preview_image {
    z-index: 2;
    position: absolute;
    width: 92%;
    height: 91%;
    object-fit: contain;
    background-color: black;
    margin: 6px;
}
.uploadnftpopup-input.upload-secound-input.inputtdrag {
    max-width: 150px;
    height: 150px;
}
.uploadnftpopup-input.upload-secound-input.inputtdrag input {
    z-index: 999;
    position: absolute;
    opacity: 0;	
    cursor: pointer;
    width: 100%;
}
